.skill {
    .skillContent {
        min-height: $min-height;
        width: 100%;
        position: relative;
        overflow: hidden;
        display: grid;
        grid-template-columns: 38% 1fr 1fr;
        grid-template-rows: 70% 30%;
        grid-template-areas: 
        "L E E"
        "O O H";

        .languagesFrameworks, .experiences, .otherSkills, .hobbies {
            background: $color-5;
            border-radius: $border-radius;
            padding: 1.8rem;
        }

        h3 {
            font-size: 1.4rem;
            margin-bottom: 1rem;
            font-family: $font-1;
            color: $black;
            text-transform: uppercase;
        }

        .languagesFrameworks {
            grid-area: L;
            margin-bottom: .5rem;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr;
            grid-template-areas: 
            "L"
            "F";

            .languagesDisplay {
                grid-area: L;
            }

            .frameworksDisplay {
                grid-area: F;
            }

            h3 {
                margin-bottom: .2rem;
            }

            // bar progress
            .years {
                font-size: .6rem;
                display: flex;
                justify-content: space-between;
                color: $white;

                span {
                    width: 36%;
                    margin-right: 20px;

                    // &:nth-child(2) {
                    //     text-align: right;
                    //     width: 20%;
                    // }

                    // &:nth-child(3) {
                    //     text-align: right;
                    //     width: 44%;
                    // }
                    &:nth-child(2):before, &:nth-child(3):before, &:nth-child(4):before, &:nth-child(5):before, &:nth-child(6):before {
                        content: '';
                        background: $white;
                        position: absolute;
                        height: 175px;
                        width: 1px;
                        transform: translate(9px, 19px);
                    }
                }
            }

            .languagesList {
                padding-top: .5rem;

                li {
                    color: $black;
                }
                
                .progressBar {
                    height: .4rem;
                    background: transparent;
                    border-radius: 40px;
                    margin-top: .2rem;
                    position: relative;

                    &:after {
                        position: absolute;
                        content: '';
                        background: $color-4;
                        border-radius: 40px;
                        height: .4rem;
                        width: 0;
                        animation: line 2.5s ease-out forwards;

                        @keyframes line {
                            to {
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
        .experiences {
            grid-area: E;
            margin: 0 0 .5rem .5rem;
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 10% 1fr 1fr;
            grid-template-areas: 
              "1"
              "2"
              "3";
            
            h3 {
              grid-area: 1;
            }
            h4 {
              font-size: 1.1rem;
            }
            h5 {
              padding: 0.5rem 0 0.5rem 1rem;
            }
            p {
              text-align: justify;
              padding: 0 1rem;
              margin-bottom: 15px;
            }
            .xp-1 {
              grid-area: 2;
            }
            .xp-2 {
              grid-area: 3;
            }
          }
          .otherSkills {
            grid-area: O;
            margin-right: 0.5rem;
      
            .list {
              display: flex;
              width: 80%;
              height: 100%;
              margin: 0 auto;
              justify-content: space-between;
      
              ul {
                height: 80%;
                display: grid;
      
                i {
                  margin-right: 2.2rem;
                }
              }
            }
          }
          .hobbies {
            grid-area: H;
            padding-left: 1.5rem;
      
            ul {
              height: 80%;
              display: grid;
      
              li {
                // i {
                //   position: absolute;
                //   color: $color-5;
                // }
                span {
                  position: absolute;
                  transform: translateX(2.2rem);
                }
              }
            }
          }
        }
      }
      @media screen and (max-width: 950px){
        .skillContent {
          h3 {
            font-size: 1.2rem !important;
          }
          .languagesList {
            padding-top: .2rem !important;
          }
          p {
            font-size: .8rem;
          }
          .otherSkills, .hobbies {
            font-size: .8rem;
          }
        }
      }
      @media screen and (max-width: 850px){
        .skillContent {
          h4 {
            font-size: .9rem !important;
          }
          h5 {
            font-size: .6rem !important;
            padding: .2rem 1rem !important;
          }
        }
      }
      @media screen and (max-width: 500px){
        .skillContent {
          display: grid !important;
          grid-template-columns: 1fr !important;
          grid-template-rows: 1fr 1fr 180px 180px !important;
          
          .languages {
            grid-area: 1 !important;
          }
          .experiences {
            grid-area: 2 !important;
            margin-left: 0 !important;
          }
          .otherSkills {
            grid-area: 3 !important;
            margin-right: 0 !important;
          }
          .hobbies {
            grid-area: 4 !important;
            margin-top: .5rem !important;
          }
        }
    }
